:root {
  font-size: 16px;
  box-sizing: border-box;
}

@font-face {
  font-family: "Space Mono";
  src: url("../fonts/SpaceMono-BoldItalic.woff2") format("woff2");
  font-weight: bold;
  font-style: italic;
}
@font-face {
  font-family: "Space Mono";
  src: url("../fonts/SpaceMono-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "Space Mono";
  src: url("../fonts/SpaceMono-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Space Mono";
  src: url("../fonts/SpaceMono-Italic.woff2") format("woff2");
  font-weight: normal;
  font-style: italic;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-latin-300-normal.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/poppins-latin-500-normal.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}
h1 {
  font-family: "Space Mono";
  font-size: 96px;
  font-style: italic;
  font-weight: bold;
  line-height: normal;
  text-transform: uppercase;
}

h2 {
  font-family: "Space Mono";
  font-size: 66px;
  font-style: italic;
  font-weight: normal;
  line-height: 58px;
  text-transform: uppercase;
}

h3 {
  font-family: "Space Mono";
  font-size: 18px;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  letter-spacing: 1.8px;
  text-transform: uppercase;
}

:root {
  --color-main-bg: #ffffff;
  --color-main-text: #000000;
  --color-modal-bg: #e00000;
  --color-modal-text: #ffffff;
  --color-button-primary-bg: #d8d8d8;
  --color-button-primary-text: #000000;
  --color-button-hover-bg: #000000;
  --color-button-hover-text: #ffffff;
  --color-form-bg-default: #ffffff;
  --color-form-bg-hover: #ffd6d6;
  --color-form-bg-active: #fe5858;
  --color-form-bg-selected: #e00000;
  --color-form-text-default: #000000;
  --color-form-text-secondary: #313144;
  --color-form-text-selected: #ffffff;
}

/* Global Styles */
html, body {
  margin: 0;
  min-height: 100%;
  font-family: "Space Mono", monospace;
}

/* Utility Classes */
.hidden {
  display: none !important;
}

/* Layout */
.main-container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0;
  background-color: var(--color-main-bg);
  box-sizing: border-box;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.site-content {
  flex: 1;
}

/* Masthead Styles */
#masthead {
  position: sticky;
  top: 0;
  z-index: 1000;
  height: 5rem;
  background: #FFF;
  box-shadow: 0px 4px 14px 10px rgba(0, 0, 0, 0.03);
}

#masthead .logo-menu-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  background: #FFF;
}

#masthead .logo-menu-wrapper .logo-container {
  padding-left: clamp(1rem, 5vw + 2rem, 9.125rem);
}

#masthead .logo-menu-wrapper .logo-container img {
  height: auto;
  width: 13.5rem;
}

/* Primary Navigation Styles */
#primary-navigation {
  display: flex;
}

#primary-navigation .main-menu {
  display: flex;
  list-style: none;
  margin: 0;
  padding-right: clamp(1rem, 5vw + 2rem, 9.125rem);
  gap: 2.8125rem;
}

#primary-navigation .main-menu li a {
  text-decoration: none;
  color: var(--color-main-text);
}

#primary-navigation .main-menu li a:hover {
  font-weight: bold;
  text-decoration: none;
}

/* Menu Button Styles */
.menu__button {
  position: absolute;
  right: 40.6px;
  display: none;
  justify-content: center;
  align-items: center;
  width: 27.4px;
  height: 18.8px;
  padding: 0;
  background: transparent;
  border: none;
  cursor: pointer;
}

.menu__button--line,
.menu__button--line::before,
.menu__button--line::after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  border-radius: 1px;
  background-color: #000;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.menu__button--line::before {
  top: -9.4px;
}

.menu__button--line::after {
  top: 9.4px;
}

/* Media Queries */
@media (max-width: 768px) {
  #masthead .logo-menu-wrapper .logo-container img {
    width: 8.625rem;
  }
  #primary-navigation {
    display: none;
  }
  #primary-navigation.menu-active {
    display: flex;
    position: absolute;
    top: 100%; /* Position the menu right below the parent */
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: var(--color-modal-bg);
    justify-content: center;
  }
  #primary-navigation.menu-active .main-menu {
    flex-direction: column;
    text-align: center;
    margin-top: 18rem;
    gap: 1.125rem;
  }
  #primary-navigation.menu-active .main-menu li a {
    font-size: 2.75rem;
    color: var(--color-modal-text);
  }
  .menu__button {
    display: flex;
  }
  .menu__button.menu-active .menu__button--line {
    background-color: transparent !important;
  }
  .menu__button.menu-active .menu__button--line::before {
    transform: rotate(45deg) translate3d(7px, 4px, 0);
  }
  .menu__button.menu-active .menu__button--line::after {
    transform: rotate(-45deg) translate3d(8px, -6px, 0);
  }
}
/* Animations */
#primary-navigation.menu-active:not(.menu-closing) {
  animation: openMenu 0.5s ease-out forwards;
}

.menu-closing {
  animation: closeMenu 0.5s ease-out forwards;
}

@keyframes closeMenu {
  from {
    transform: translateY(0);
    opacity: 1;
  }
  to {
    transform: translateY(100%);
    opacity: 0;
  }
}
@keyframes openMenu {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.site-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 31px;
  padding-top: 1px;
  padding-bottom: 8px;
  border-top: solid 1px var(--color-main-text);
}
.site-footer__navigation {
  align-self: center;
}
.site-footer__navigation--menu {
  display: flex;
  list-style: none;
  gap: 5.375rem;
  padding: 0;
  margin: 0;
}
.site-footer__navigation--menu .menu-item {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: normal;
}
.site-footer__navigation--menu .menu-item a {
  color: var(--color-main-text);
  text-decoration: none;
}
.site-footer__navigation--menu .menu-item a:hover {
  font-weight: bold;
  text-decoration: none;
}

/* ==========================================================================
   MEDIA QUERIES
   ========================================================================== */
/* Media Queries - NAVIGATION
   ========================================================================== */
/* TABLET breakpoint */
@media (max-width: 768px) {
  .site-footer {
    height: 208px;
  }
  .site-footer__navigation--menu {
    text-align: center;
    flex-direction: column;
    gap: 2.25rem;
  }
}
/* Overlay Contact */
.contact-overlay {
  position: fixed;
  inset: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-overlay__container {
  width: 597px;
  overflow: hidden;
  border: solid var(--color-main-text);
  background-color: var(--color-main-bg);
}
.contact-overlay__container .fa-circle-xmark {
  display: none;
}
.contact-overlay__container .contact-overlay__header {
  width: 886px;
  margin-top: 18px;
}
.contact-overlay__container .contact-overlay__header .contact-overlay__header-wrapper {
  position: relative;
  right: 8.95rem;
}
.contact-overlay__container .contact-overlay__header .contact-overlay__header-wrapper:nth-of-type(2) {
  right: 6.5rem;
}

/* Contact Form 7 */
.wpcf7-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wpcf7-form p {
  margin-top: 24px;
  margin-bottom: 0px;
}
.wpcf7-form p:nth-of-type(1) {
  margin-top: 46px;
}
.wpcf7-form p > label {
  font-family: Poppins;
  font-size: 0.75rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}
.wpcf7-form-control {
  width: 263px;
  height: 40px;
  border-radius: 8px;
  border: 1px solid #B8BBC2;
  background: #FFF;
}

form.wpcf7-form input[type=text],
form.wpcf7-form input[type=email] {
  /* Your styles here */
  margin-top: 3px;
  font-family: Poppins;
  font-size: 0.75rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

/* Textarea */
.wpcf7-textarea {
  height: 210px;
  resize: none;
}

/* Submit Button */
.wpcf7-submit {
  display: block;
  margin: 0 auto;
  margin-top: 18px;
  width: 263px;
  height: 50px;
  padding: 8px 15px;
  font-family: "Space Mono";
  font-size: 0.75rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  border: none;
  border-radius: 0px;
  background-color: var(--color-button-primary-bg);
}

.hidden {
  display: none;
}

.fade-in {
  animation: fadeIn 0.5s forwards;
}

.fade-out {
  animation: fadeOut 0.5s forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(0%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(0%);
  }
}
@media (max-width: 768px) {
  .contact-overlay__container {
    width: 286px;
    height: 90vh;
  }
  .wpcf7-form-control {
    width: 190px;
  }
  .wpcf7-not-valid-tip,
  .wpcf7-response-output {
    font-size: 0.8em;
  }
  .contact-overlay__header--image {
    width: 145px;
  }
  .contact-overlay__container .contact-overlay__header {
    margin-top: 6px;
  }
  .contact-overlay__container .contact-overlay__header .contact-overlay__header-wrapper {
    right: 5.8rem;
  }
  .contact-overlay__container .contact-overlay__header .contact-overlay__header-wrapper:nth-of-type(2) {
    right: 4.4rem;
  }
  .contact-overlay__container .fa-circle-xmark {
    position: relative;
    top: 0.4%;
    left: 90%;
    display: inline-block;
    font-size: x-large;
    cursor: pointer;
  }
  .contact-overlay__container .fa-circle-xmark:active {
    filter: invert(1);
  }
}